    -webkit-animation: App_fadeAppear__18Rue 1s cubic-bezier(0.64, 0.57, 0.67, 1.53);
            animation: App_fadeAppear__18Rue 1s cubic-bezier(0.64, 0.57, 0.67, 1.53);
