  @-webkit-keyframes App_fadeAppear__18Rue {
    0% {
    opacity: 0.01;
    -webkit-transform: translateX(-400px) scale(0.3) rotate(-90deg);
            transform: translateX(-400px) scale(0.3) rotate(-90deg);
    }
  
   100% {
    opacity: 1;
    -webkit-transform: translateX(0px) scale(1) rotate(0deg);
            transform: translateX(0px) scale(1) rotate(0deg);
  }
}

  @keyframes App_fadeAppear__18Rue {
    0% {
